<template>
  <div>

<Row  type="flex" justify="start">
      <Col :span="6" >
       <p></p>
      </Col>
      <Col :span="13" >
      </Col>
    </Row>


    <Row    type="flex" justify="center" style="margin-top:0.5em;">

      <Col :span="8"  >
      <Affix :offset-top="82" >
      <Row  type="flex" :gutter="10" justify="space-around">
            <Col :span="12"  >
            <Card  style="background: transparent;" >
                <p slot="title">HTIWH保研算法</p>
                <Button  slot="extra" type="text" shape="circle" @click="showRGMD()" size="small" icon="ios-information"></Button>
                <Spin v-if="s_loading" fix></Spin>
                <p style="font-size:2.2em">{{s_rgmd}}<span style="font-size:16px">/100</span></p>

            </Card>
            </Col>
            <Col :span="12" >
            <Card style="background: transparent;">
                <p slot="title">加权平均分算法</p>
                <Spin v-if="s_loading" fix></Spin>
                <p style="font-size:2.2em">{{s_wa}}<span style="font-size:16px">/100</span></p>

            </Card>
            </Col>
        </Row>
            <Row  type="flex" style="margin-top:1.125em" :gutter="10" justify="space-around">
            <Col :span="12" >
            <Card style="background: transparent;">
                <p slot="title">标准4.0算法</p>
                    <Button  slot="extra" type="text" shape="circle" @click="showS4()" size="small" icon="ios-information"></Button>
                    <Spin v-if="s_loading" fix></Spin>
                    <p style="font-size:2.2em">{{s_s4}}<span style="font-size:16px">/4.0</span></p>
                

            </Card>
            </Col>
            <Col :span="12" >
            <Card>
                <p slot="title">标准改进(一)算法</p>
                 <Button  slot="extra" type="text" shape="circle" @click="showP1S4()" size="small" icon="ios-information"></Button>
                <Spin v-if="s_loading" fix></Spin>
                <p style="font-size:2.2em">{{s_p1s4}}<span style="font-size:16px">/4.0</span></p>

            </Card>
            </Col>
        </Row>
        <Row  type="flex" style="margin-top:1.125em"  :gutter="10" justify="space-around">
            <Col :span="12" >
            <Card style="background: transparent;">
                <p slot="title">标准改进(二)算法</p>
                <Button  slot="extra" type="text" shape="circle" @click="showP2S4()" size="small" icon="ios-information"></Button>
                <Spin v-if="s_loading" fix></Spin>
                <p style="font-size:2.2em">{{s_p2s4}}<span style="font-size:16px">/4.0</span></p>
            </Card>
            </Col>
            <Col :span="12" >
            <Card style="background: transparent;">
                <p slot="title">北大算法</p>
                <Button  slot="extra" type="text" shape="circle" @click="showPK()" size="small" icon="ios-information"></Button>
                <Spin v-if="s_loading" fix></Spin>
                <p style="font-size:2.2em">{{s_pk}}<span style="font-size:16px">/4.0</span></p>

            </Card>
            </Col>
        </Row>
        <Row  type="flex"  style="margin-top:1.125em"  :gutter="10" justify="space-around">
            <Col :span="12" >
            <Card style="background: transparent;">
                <p slot="title">WES算法</p>
                <Button  slot="extra" type="text" shape="circle" @click="showWES()" size="small" icon="ios-information"></Button>
                <Spin v-if="s_loading" fix></Spin>
                <p style="font-size:2.2em">{{s_wes}}<span style="font-size:16px">/4.0</span></p>

            </Card>
            </Col>
            <Col :span="12" >

            </Col>
        </Row>
        </Affix>



      </Col>
      <Col :span="14" :offset="1" >

    <Alert show-icon closable>
        <Icon type="ios-lightbulb-outline" slot="icon"></Icon>
        <span slot="close">隐藏</span>
        <template slot="desc">我的课程：对于重修课程，取最高分显示。<br>刷分计算器：查看增改课程对于GPA的影响。
        <br>国外大学录取标准：最新的国外大学的录取硬性指标的统计，包括GPA、GRE、TOEFL。 </template>
    </Alert>
        <Tabs >
        <TabPane label="我的课程">
            <Row type="flex" justify="center">
                <Col :span="24" style="background: transparent;" >
                    <Table  ref="courses" @on-selection-change="get_selections" size="small" border :columns="coursesColumns" :data="courses"></Table>
                </Col>
            </Row>

        </TabPane>
            <TabPane disabled label="刷分计算器" >标签二的内容</TabPane>
            <TabPane disabled label="国外大学录取标准">标签三的内容</TabPane>
        </Tabs>

 


          
      </Col>
    </Row>


<BackTop></BackTop>

</div>
</template>

<script>
// let TWEEN = require('@tweenjs/tween.js');
export default {
  data() {
    return {
      courses:[],
      s_zu:0.0,
      s_loading:false,
      selections:[],
      coursesColumns:[
                   {
                        type: 'selection',
                        align: 'center',
                        width: 60,
                    },
                    {
                        title: '课程号',
                        key: 'number',
                        width: 130,
                        ellipsis:true,
                        align: 'center',
                        render: (h, params) => {
                            return h('Tooltip', {
                              props: {
                                    content: params.row.number,
                                    placement: "top"
                                }
                            },[
                                h('tag', params.row.number)
                            ]);
                        }
                    },
                    {
                        title: '课程名称',
                        key: 'name',
                        align: 'center',
                        render: (h, params) => {
                            return h('Tooltip', {
                              props: {
                                    content: params.row.name,
                                    placement: "top"
                                }
                            },[
                                h('tag', params.row.name)
                            ]);


                        }
                    },
                    {
                        title: '学分',
                        align: 'center',
                        key: 'credit',
                        width: 80,
                        render: (h, params) => {
                            return h('div', [
                                h('tag', params.row.credit)
                            ]);
                        }
                    },
                    {
                        title: '类型',
                        align: 'center',
                        width: 100,
                        key: 'course_type',
                        render: (h, params) => {
                            const row = params.row;
                            const color = row.course_type === 1 ? 'green' : 'red';
                            const text = row.course_type === 1 ? '考试课' : '考查课';

                            return h('Tag', {
                                props: {
                                    // type: 'dot',
                                    // color: color
                                }
                            }, text);
                        }
                    },
                    {
                        title: '成绩',
                        align: 'center',
                        key: 'score',
                        width: 85,
                        render: (h, params) => {
                            return h('div', [
                                h('tag', params.row.score)
                            ]);
                        }
                    },
                    {
                        title: '操作',
                        key: 'action',
                        width: 120,
                        align: 'center',
                        render: (h, params) => {
                            return h('ButtonGroup',{
                                props: {
                                    shape: 'circle',
                                }
                            },  [
                                h('Button', {
                                    props: {
                                        type: 'ghost',
                                        size: 'small',
                                        icon: 'edit'
                                    },
                                    on: {
                                        click: () => {
                                            this.show(params.index)
                                        }
                                    }
                                }, '编辑'),
                            ]);
                        }
                    }
                ]

  }
},
computed: {
    s_wa() {
        let all_credits = 0.0;
        let temp = 0.0;
        for (var i = this.selections.length - 1; i >= 0; i--) {
            all_credits = all_credits + parseFloat(this.selections[i].credit);
            temp = temp + parseFloat(this.selections[i].credit)* parseFloat(this.selections[i].score);
        }
        let s_wa = temp/all_credits;
        return s_wa.toFixed(2);;
    },
    s_rgmd() {
        let all_rgmd_credits = 0.0;
        let temp = 0.0;
        for (var i = this.selections.length - 1; i >= 0; i--) {
            if (this.selections[i].course_type == 1){
                all_rgmd_credits = all_rgmd_credits + parseFloat(this.selections[i].credit);
                temp = temp + parseFloat(this.selections[i].credit)* parseFloat(this.selections[i].score);
            }
        }
        let s_rgmd = temp/all_rgmd_credits;
        return s_rgmd.toFixed(2);
    },
    s_s4() {
        let all_credits = 0.0;
        let temp = 0.0;
        for (var i = this.selections.length - 1; i >= 0; i--) {
            all_credits = all_credits + parseFloat(this.selections[i].credit);
            let score = parseFloat(this.selections[i].score);
            // let g_score = score*4/100;
            let g_score = 0.0;
            if (score <= 100 & score >= 90 ) {
                g_score = 4.0;
            }
            else if (score <= 89 & score >= 80) {
                g_score = 3.0;
            }
            else if (score <= 79 & score >= 70) {
                g_score = 2.0;
            }
            else if (score <= 69 & score >= 60) {
                g_score = 1.0;
            }
            else {
                g_score = 0.0;
            }
            temp = temp + parseFloat(this.selections[i].credit)* g_score;
        }
        let s_s4 = temp/all_credits;
        return s_s4.toFixed(2);
    },
    s_p1s4() {
        let all_credits = 0.0;
        let temp = 0.0;
        for (var i = this.selections.length - 1; i >= 0; i--) {
            all_credits = all_credits + parseFloat(this.selections[i].credit);
            let score = parseFloat(this.selections[i].score);
            let g_score = 0.0;
            if (score <= 100 & score >= 85 ) {
                g_score = 4.0;
            }
            else if (score <= 84 & score >= 70) {
                g_score = 3.0;
            }
            else if (score <= 69 & score >= 60) {
                g_score = 2.0;
            }
            else {
                g_score = 0.0;
            }
            temp = temp + parseFloat(this.selections[i].credit)* g_score;
        }
        let s_p1s4 = temp/all_credits;
        return s_p1s4.toFixed(2);
    },
    s_p2s4() {
        let all_credits = 0.0;
        let temp = 0.0;
        for (var i = this.selections.length - 1; i >= 0; i--) {
            all_credits = all_credits + parseFloat(this.selections[i].credit);
            let score = parseFloat(this.selections[i].score);
            let g_score = 0.0;
            if (score <= 100 & score >= 85 ) {
                g_score = 4.0;
            }
            else if (score <= 84 & score >= 75) {
                g_score = 3.0;
            }
            else if (score <= 74 & score >= 60) {
                g_score = 2.0;
            }
            else {
                g_score = 0.0;
            }
            temp = temp + parseFloat(this.selections[i].credit)* g_score;
        }
        let s_p1s4 = temp/all_credits;
        return s_p1s4.toFixed(2);
    },
    s_pk() {
        let all_credits = 0.0;
        let temp = 0.0;
        for (var i = this.selections.length - 1; i >= 0; i--) {
            all_credits = all_credits + parseFloat(this.selections[i].credit);
            let score = parseFloat(this.selections[i].score);
            let g_score = 0.0;
            if (score >= 90& score <= 100) {
                g_score = 4.0;
            }
            else if (score >= 85) {
                g_score = 3.7;
            }
            else if (score >= 82) {
                g_score = 3.3;
            }
            else if (score >= 78) {
                g_score = 3.0;
            }
            else if (score >= 75) {
                g_score = 2.7;
            }
            else if (score >= 72) {
                g_score = 2.3;
            }
            else if (score >= 69) {
                g_score = 2.0;
            }
            else if (score >= 66) {
                g_score = 1.7;
            }
            else if (score >= 63) {
                g_score = 1.3;
            }
            else if (score >= 60) {
                g_score = 1.0;
            }
            else {
                g_score = 0.0;
            }
            temp = temp + parseFloat(this.selections[i].credit)* g_score;
        }
        let s_pk = temp/all_credits;
        return s_pk.toFixed(2);
    },
    s_wes() {
        let filters_0 = ["体育", "职业生涯规划及就业指导课程", "军训及军事理论"];
        let filters_1 = ["毛泽东思想和中国特色社会主义理论体系概论",
                         "思想道德修养与法律基础",
                         "中国近现代史纲要",
                         "大学英语限选",
                         "马克思主义基本原理"];
        function contains(a, obj) {
            for (var i = 0; i < a.length; i++) {
                if (a[i] === obj) {
                    return true;
                }
            }

            return false;
        }
        let all_credits = 0.0;
        let temp = 0.0;
        for (var i = this.selections.length - 1; i >= 0; i--) {
            let credit = parseFloat(this.selections[i].credit);
            if (contains(filters_0, this.selections[i].name))  {
                credit = 0.0;
            }
            if (contains(filters_1, this.selections[i].name))  {
                credit = 1.0;
            }
            all_credits = all_credits + credit;
            let score = parseFloat(this.selections[i].score);
            let g_score = 0.0;
            if (score <= 100 & score >= 85 ) {
                g_score = 4.0;
            }
            else if (score <= 84 & score >= 75) {
                g_score = 3.0;
            }
            else if (score <= 74 & score >= 60) {
                g_score = 2.0;
            }
            else {
                g_score = 0.0;
            }


            temp = temp + credit* g_score;
        }
        let s_wes = temp/all_credits;
        return s_wes.toFixed(2);
    },
},
watch: {
    // rgmd: function(newValue, oldValue) {
    //   var vm = this;
    //   function animate (time) {
    //     requestAnimationFrame(animate);
    //     TWEEN.update(time);
    //   }
    //   new TWEEN.Tween({ tweeningNumber: oldValue })
    //     .easing(TWEEN.Easing.Quadratic.Out)
    //     .to({ tweeningNumber: newValue }, 500)
    //     .onUpdate(() => {
    //       vm.s_rgmd = this.tweeningNumber.toFixed(2);
    //     })
    //     .start()
    //   animate();
    // }
},
mounted() {
  this.courses = this.$ls.get('courses', []);
  if (this.courses.length == 0) {
    this.$router.push('/');
    this.$Message.error('登录信息已失效，请重新登录');
  }
  this.selections = this.courses;
},
created() {
},
methods: {
    showRGMD() {
        this.$Notice.info({
            title: '提示',
            duration: 20,
            desc: '从教务系统获取的课程成绩，由于刷分或者重修的原因，可能不会包含所有参加过的考试成绩。因此对于重修过的同学，这里计算的保研成绩与实际保研成绩将略有偏差，请自行校对每门重修课程的成绩。'
        });
    },
    showS4() {
        this.$Notice.info({
            title: '标准4.0算法',
            duration: 20,
            desc: '100-90 对应 4.0 <br>  89-80 对应 3.0 <br>  79-70 对应 2.0 <br>  69-60 对应 1.0'
        });

    },
    showP1S4() {
        this.$Notice.info({
            title: '标准改进（二）算法',
            duration: 20,
            desc: '100-85 对应 4.0 <br>  84-70 对应 3.0 <br>  69-60 对应 2.0'
        });

    },
    showPK() {
        this.$Notice.info({
            title: '北大算法',
            duration: 20,
            desc: '100-90 对应 4.0 <br> 89-85 对应 3.7 <br> 84-82 对应 3.3 <br> 81-78 对应 3.0  <br> 77-75 对应 2.7 <br> 74-72 对应 2.3 <br> 71-68 对应 2.0 <br> 67-64 对应 1.5 <br> 63-60 对应 1.0'
        });

    },
    showP2S4() {
        this.$Notice.info({
            title: '标准改进（二）算法',
            duration: 20,
            desc: '100-85 对应 4.0 <br>  84-75 对应 3.0 <br>  74-60 对应 2.0'
        });

    },
    showWES() {
        this.$Notice.info({
            title: 'WES算法',
            duration: 20,
            desc: '100-85 对应 4.0 <br>  84-75 对应 3.0 <br>  74-60 对应 2.0 <br> <br> 将 "体育", "职业生涯规划及就业指导课程", "军训及军事理论"调整为<Tag>0</Tag>学分；<br> 将"毛泽东思想和中国特色社会主义理论体系概论","思想道德修养与法律基础","中国近现代史纲要","大学英语限选","马克思主义基本原理"调整为<Tag>1</Tag>学分 '
        });

    },
    get_selections() {
        this.selections = arguments[0];
        if (this.selections.length == 0) {
            this.s_loading = true;
            this.$Message.loading("等待用户选择任何课程参与成绩计算")
        }
        else {
            this.s_loading = false;
        }
    }

}
}
</script>

<style scoped>

    .gpa-spin-container{
        display: inline-block;
        width: 200px;
        height: 100px;
        position: relative;
        border: 1px solid #eee;
    }
</style>



