import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Main from '@/components/Main'
import Calculator from '@/components/Calculator'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'calculator',
      component: Main,
      children: [
        {
          path: '',
          component: Login
        },
        {
          path: 'calculator',
          component: Calculator
        }
      ]
    },
  ]
})



