<template>
  <div>
    <Row type="flex"  justify="center" >

      <Col :xs="12" :sm="8" :md="6" :lg="6" :xl="6">


        <Card  class="loginCard" :padding="0" style="background: transparent;">
          <p slot="title" style="font-size:1.3em;font-weight:normal">
            连接教务系统 
          </p>
          <Button  slot="extra" type="text" shape="circle" @click="showHelp()" size="small" icon="ios-information"></Button>
          <Row  type="flex" justify="center">
            <Col :span="18" >

              <Form class="loginform" ref="loginForm" :model="loginForm" :rules="loginFormRules">
                <FormItem prop="studentID">
                  <Input type="text" v-model="loginForm.studentID" @keyup.enter.native="submitForm('loginForm')" placeholder="学号">
                    <Icon type="ios-person-outline" slot="prepend"></Icon>
                  </Input>
                </FormItem>
                <FormItem prop="password">
                  <Input type="password" v-model="loginForm.password" @keyup.enter.native="submitForm('loginForm')" placeholder="口令">
                    <Icon type="ios-locked-outline" slot="prepend"></Icon>
                  </Input>
                </FormItem>
                <FormItem>
                  <Button class="loginbuttons"  type="primary"  @click="submitForm('loginForm')">提交</Button>
                  <Button class="loginbuttons"  type="ghost" @click="resetForm('loginForm')">重置</Button>
                </FormItem>
              </Form>
            </Col>
          </Row>
        </Card>
      </Col>
    </Row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loading:false,
      loginForm: {
        studentID: '',
        password: '',
      },
      loginFormRules: {
        studentID: [
        { required: true, message: '请输入学号', trigger: 'blur' }
        ],
        password: [
        { required: true, message: '请输入口令', trigger: 'blur' }
        ]
      }
    };
  },
  methods: {
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    submitForm(formName) {
      this.$Loading.start();
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$request.post('/api/v1/courses', {
            stuid: this.loginForm.studentID,
            pwd: this.loginForm.password
          })
          .then((response) => {
            if (response.data.courses.length > 0){
              this.$ls.set('courses', response.data.courses,5 * 60 * 1000);
              this.$Message.info({
                content: '成功获得您的课程信息',
                closable: true
              });
              this.$router.push('/calculator');
              this.$Loading.finish();

            }
            else {
              this.$Message.error('与教务系统交互失败，请确认账户信息正确');
              this.$Loading.error();
            }

          })
          .catch((error) => {
            this.$Message.error('服务器维护中');
            this.$Message.error('This is an error tip');
            console.log(error);
            this.$Loading.error();
          });
        } else {
          this.$Message.error('信息填写不完整');
          this.$Loading.error();
        }
      });
    },
    handleSelect() {
      // console.log(arguments[0])
    },
    showHelp() {
      this.$Notice.info({
        title: '声明',
        desc: '本工具不会采集用户的账户信息。查看源码可了解更多细节。'
      });

    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.loginform {
  margin:30px 20px 0px 20px;
}
.loginbuttons {
  margin: 10px 20px 0px 20px;
}

.loginCard {
  margin-top: 10em;
  box-shadow: 0px 4px 16px 0px rgba(0, 0, 0, .10), 0px 0px 12px 0px rgba(0, 0, 0, .04)
}

</style>



